export default {
  props: {
    versionInfo: {
      type: Object
    },
    appInfo: {
      type: Object
    }
  },
  data() {
    return {
      show: false,
      updataContent: '',
      showinDownLoadPage: false,
      downloadUrl: ''
    }
  },
  created() {
    setTimeout(() => {
      this.show = true
      this.downloadUrl = `${this.axios.defaults.baseURL}${this.appInfo.shortUrl}`
    }, 200)
  },
  methods: {
    cancel() {
      this.show = false
      setTimeout(() => {
        this.$emit('cancel')
      }, 500)
    },
    sure() {

    },
    clickcontent() {

    },
    getDownLoadCount(count) {
      if (count) {
        return count
      } else {
        return 0
      }
    },
    getAllowDownLoadCount(strategy) {
      if (strategy && strategy.downloadCountLimit) {
        return strategy.downloadCountLimit
      } else {
        return '不限'
      }
    }
  }
}



