import Bus from '../../common/js/bus'

export default {
  data() {
    return {
      slotName: 'main-nav'
    }
  },
  created() {
    Bus.$on('appdetail', () => {
      this.slotName = 'detail-nav'
    })
    Bus.$on('applist', () => {
      this.slotName = 'main-nav'
      this.$router.push('/apps')
    })
  },
  methods: {
    clickSubItem(data) {
      if (data.index === '应用概述') {
        Bus.$emit('appSummary')
      }
      if (data.index === '应用设置') {
        Bus.$emit('appSetting')
      }
      if (data.index === 'API文档') {
        let href = `${this.axios.defaults.baseURL}api/swagger`
        window.open(href, '_blank')
      }
      if (data.index === 'Help') {
      }
      if (data.index === 'GitHub') {
      }
      if (data.index === 'About') {
      }
    },
    gotoApiDoc() {

    }
  },
  watch: {
    '$route': (to) => {
    }
  }
}



